.about {
  font-size: 20px;
}

.about__heading {
  text-align: center;
  margin: 20px 0;
  font-size: 36px;
}

.about__main-text {
  width: 80%;
  margin: 0 auto;
}

.about__text-container {
  display: flex;
}

.about__paragraph {
  margin: 30px 0;
}

.about__photo {
  min-width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 50%;
  margin: 10px 0 10px 60px;
  float: right;
}

.about__list-name {
  margin: 50px 0 0;
}

.about__list-item:before {
  content: "";
  width: 5px;
  height: 5px;
  position: relative;
  background-color: red;
}

.about a {
  color: #000;
  transition: all 0.2s;
}

.about a:hover {
  color: var(--c1);
}

@media (max-width: 800px) {
  .about__main-text {
    display: flex;
    flex-direction: column;
    font-size: 16px;
  }

  .about__list-name {
    margin: 10px 0 0;
  }

  .about__paragraph {
    margin: 10px 0;
  }

  .about__photo {
    margin: 0 auto 20px;
  }
}
